<template>
  <div class="container">
    <h1>WellCome to Fullstack with vuejs + nodejs + mongoDB</h1>
    <div class="createPost">
      <input type="text" v-model="text" placeholder="Add....." />
      <button @click="createPost">AddPost</button>
    </div>
    <br />
    <p class="error" v-if="error">{{ error }}</p>
    <div class="acticle">
      <div
        class="content"
        v-for="(post, index) in posts"
        :index="index"
        :key="post._id"
        @dblclick="deletePost(post._id)"
      >
        {{
          `${post.createAt.getDate()}/ ${post.createAt.getMonth()}/${post.createAt.getFullYear()}`
        }}
        <p>{{ post.text }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import PostService from "../PostService.js";
export default {
  name: "PostComponent",
  data() {
    return {
      posts: [],
      error: "",
      text: "",
    };
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
        console.log('this.posts')
    } catch (e) {
      this.error = e.message;
    }
  },
  methods: {
    async createPost() {
      await PostService.insertPost(this.text);
      this.posts = await PostService.getPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    },
  },
};
</script>

